import React, { useEffect } from "react";
import { Table } from "react-bootstrap";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import addNoteIcon from "../../icons/plus-circle.svg";
import NotesModal from "../notesModal";
import "./table.css";
const DynamicTable = ({}) => {
  const [tableData, setTableData] = React.useState([]);
  const [show, setShow] = React.useState(false);
  const [selectedRow, setSelectedRow] = React.useState(null);
  useEffect(() => {
    setTableData([
      {
        id: 0,
        completeDate: new Date(),
        completeGroup: "Group 1",
        deadLine: new Date(),
        lastNote: "Signed by user",
        note: "",
      },
      {
        id: 1,
        completeDate: new Date(),
        completeGroup: "Group 2",
        deadLine: new Date(),
        lastNote: "Signed by user",
        note: "",
      },
    ]);
  }, []);

  
  function handleModalVisibility(index){
    setShow(true);
    //find the object in the array and set it to selectedRow
    setSelectedRow(tableData.find((item) => {
      // console.log("the item is :", item);
      return item.id === index;
    } )); 
  }

  function handleDeadline(date, index) {
    //find the row on the base of index and update the deadline
    const updatedTableData = tableData.map((row, i) => {
      if (i === index) {
        row.deadLine = date;
      }
      return row;
    });
    setTableData(updatedTableData);
  }

  function handleNotes(value){
    //change the value of notes in selectedRow
    setSelectedRow({...selectedRow, note: value});
    //find the row on the base of index and update the notes
    const updatedTableData = tableData.map((row, i) => {
        
        if (row.id === selectedRow.id) {
            row.note = value;
        }
        return row;
        });
        setTableData(updatedTableData);
  }


  return (
    <>
      <Table striped bordered hover className="m-5 p-5 table-style">
        <thead>
          <tr>
            <th>Complete Date</th>
            <th>Complete Group</th>
            <th>Key Date</th>
            <th>Deadline</th>
            <th>Last Note</th>
            <th>Add Note</th>
          </tr>
        </thead>
        <tbody>
          {tableData?.map((item, index) => (
            <tr key={index}>
              <td>{item.completeDate.toLocaleDateString()}</td>
              <td>{item.completeGroup}</td>
              <td>{item.deadLine.toLocaleDateString()}</td>
              <td>
                <DatePicker
                  selected={new Date(item.deadLine)}
                  onChange={(date) => handleDeadline(date, index)}
                  dateFormat="MM/dd/yyyy"
                />
              </td>
              <td>{item.lastNote}</td>
              <td>
                <img
                  src={addNoteIcon}
                  onClick={() => handleModalVisibility(index)}
                ></img>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      <NotesModal show={show} handleClose={() => setShow(false)} handleSave={ handleNotes } note={selectedRow ? selectedRow.note : "No note found in selected row"}/>
    </>
  );
};

export default DynamicTable;
